
.property-container{
	width: 82%;
	padding-left: 10px;
	padding-right: 10px;
	z-index: 150;
	margin-left: auto;
	margin-right: auto;
	padding-top: 35px;
	padding-bottom: 30px;
	background: white;
	position: relative;
	overflow: auto;
	margin-top: 0px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.property-list {
	margin-top: 0;
    min-height: 700px;
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.property-item{
    flex-basis: 31%;
	width: 31%;
	color: #666;
	display: block;
	margin-bottom: 30px;
	font-family: "Gilroy-SemiBold";
	font-size: 15px;
	line-height: 22px;
}

@media screen and (max-width:1025px) {
    .property-container{
		margin-top: 70px;
	}

    .property-item{
        flex-basis: 100%;
    }
}
